<template>
<section>
  <Nav></Nav>
  <section class="section">
    <div class="container" v-if="events.length>0">
        <!--<div class="content" :key="item.objectId" v-for="item in events">
            <div class="item">
                <h3>
                    <router-link :to="{path: `/event/${item.objectId}`}">{{item.title}}</router-link>
                </h3>
            </div>
        </div>--> 
    </div>  
    <div class="container section loading" v-else><span class="_loader"></span></div>     
  </section>
</section>
 
</template>
<script>
  import Nav from '../../components/nav'
  import Footer from '../../components/footer'
  import {getEventList} from '../../api/index'
  export default {
    data() {
      return {
          events: [],
            columns: [
                {
                    field: 'title',
                    label: '赛事名称',
                },
                {
                    field: 'date',
                    label: '比赛日期',
                },
                {
                    field: 'location',
                    label: '地点',
                    centered: true
                }
            ]
      }
    },
    watch: {
    },
    methods: {
        fetchList: function(){
            var that=this;
            getEventList(res=>{
                that.events=res;
            })
        }
    },
    components: {
      Nav,
      Footer,
    },
    mounted(){
        this.fetchList();
    }
  };
</script>



